import React, { Fragment } from 'react';
import Navbar from '../layout/Navbar';
import '../../cards/Cards.css';
import Me from '../../assets/aaqiel.jpg';
const AboutPage = () => {
	return (
		<Fragment>
			<div className='about-page'>
				<Navbar />
				<div className='about-box'>
					<img src={Me} alt='photo' className='profile-photo' />
					<div className='about-info'>
						<h1>About Aaqiel Behardien</h1>
						<p style={{ width: '800px', fontSize: '20px' }}>
							Lorem ipsum, dolor sit amet consectetur adipisicing elit. Quas aliquam
							reprehenderit id nihil, veniam eius quisquam iure vero asperiores, iste
							dolorum adipisci neque ducimus voluptas doloribus magni odio unde nobis!
						</p>
					</div>
				</div>
				<a
					rel='noopener noerferrer'
					target='_blank'
					href='https://drive.google.com/file/d/1CMV06_59_NZ8sl8rhKd4fP-ziidEfxip/view?usp=sharing'
					className='btn btn-outline-success btn-about'
					style={{
						backgroundColor: '#7a4f3b',
						color: 'black',
						position: 'absolute',
						left: '52%',
						top: '60%',
					}}>
					Profile
				</a>
			</div>
		</Fragment>
	);
};

export default AboutPage;
